<template>
	<view class="">
		<Head title='上传其他藏品'/>
		<view class="content relative">
			<view class="fontS32 fw7 title">
				区块链
			</view>
			<view class="box">
				<view class="flex flexic" @click="show=true">
					<image src='/static/img/aa.jpg' mode="" class='brYuan'></image>
					<view class="picker font33 fontS28" >
						 <text v-for="item in BlockChain">
							 {{active.includes(item.id)?item.name+'':''}}
						 </text>
					</view>
					<u-icon :name="show?'arrow-up':'arrow-down'" color="#333333" size='18'></u-icon>
				</view>
				 <u-overlay :show="show"   @click="show = false">
					 <view class="warp" @tap.stop>
						 <view class="fontS36 textAc">
						 	区块链
						 </view>
						<view class="flexJBC flexW">
							<view class="lis flex flexic fontS32 " v-for="(item,index) in BlockChain" :key="index" :class="active.includes(item.id)?'active':''" @click="activeClick(item.id)">
								<image src="" mode="" class="brYuan" v-if="index!=0"></image><text>{{item.name}}</text>
							</view>
						</view>
					</view>
				 </u-overlay>
				 
			</view>
			<view class="fontS32 fw7 title" style="margin-top: 63rpx;">
				地址
			</view>
				 <u--textarea
				    placeholder="请输入地址"
				    border="none"
				    v-model="value"
				    @change="change"
					:autoHeight='true'
					maxlength='-1'
					
				  ></u--textarea>
				  <text class="font99 fontS18">您的ERC721或ERC1155合同在mainnet网络上的地址是什么？</text>
				  <view class="fixed btn">
				  	  <view class="btn00">
				  	    	确认
				  	  </view>
				  </view>
		</view>
	</view>
</template>
<script>
	
  export default {
		data() {
			return {
				value:'',
				columns: [[{label:'中国'}]],
				BlockChain:[{name:'所有区块链',id:1},{name:"MetaMask",id:2},{name:'Polygon',id:3},{name:"Klaytn",id:4},{name:'Solana',id:5}],
				index:0,
				show:true,
				active:[1],
			}
		},
		methods: {
			activeClick(id){
				if(id==1){
					this.active=[1]
					return
				}else{
					this.active.forEach((item,index)=>{
						if(item==1){
					    this.active.splice(index,1)
						}
					})
				}
				if(this.active.includes(id)){
					this.active.forEach((item,index)=>{
						if(item==id){
					    this.active.splice(index,1)
						}
					})
				}else{
					this.active.push(id)
				}
				// console.log(this.active)
			},
			bindPickerChange() {
				
			},
			confirm(e){
				console.log(e)
			}
		},
	}
</script>

<style lang='scss' scoped>
	.warp{
		width: 100%;
		/* height: 466rpx; */
		position: fixed;
		bottom: 0;
		padding: 40rpx 40rpx 70rpx;
		box-sizing: border-box;
		background: #FFFFFF;
		border-radius: 40rpx 40rpx 0px 0px;
		.lis{
			
			justify-content: center;
			width: 319rpx;
			height: 72rpx;
			background: #FFFFFF;
			border: 2rpx solid #0F172C;
			border-radius: 20rpx;
			line-height: 72rpx;
			text-align: center;
			margin-top: 32rpx;
			image{
				width: 48rpx;
				height: 48rpx;
				background-color: #fff;
				border: 1rpx solid #707070;
			}
		}
		.active{
			background-color: #0F172C;
			color: #fff;
		}
	}
	.btn{
		width: 100%;
		left: 0;
		padding:0 40rpx;
		box-sizing: border-box;
		bottom: 90rpx;
		.btn00{
			width: 100%;
			height: 115rpx;
			line-height: 115rpx;
			font-size: 32rpx;
			
		}
	}
	
	/deep/.u-textarea{
		border-radius: 32rpx;
		padding: 30rpx 38rpx;box-sizing: border-box;
		margin-bottom: 26rpx;
	}
	
	.picker{
		flex:1;
		/* width: 100rpx; */
	}
	.content{
		padding:72rpx 40rpx 0;
		box-sizing: border-box;
		.title{
			margin-bottom:35rpx;
		}
		.box{
			image{
				width: 48rpx;
				height: 48rpx;
				margin-right: 20rpx;
			}
			width: 100%;
			height: 106rpx;
			line-height:106rpx;
			padding:0 38rpx;
			box-sizing: border-box;
 			border-radius: 32rpx;
			background:#fff;
			.picker{
				width: 500px;
			}
		}
	}
</style>